.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.6);

  visibility: hidden;
  opacity: 0;

  transition: visibility 0.2s linear 0.5s, opacity 0.2s linear 0.5s;
}

.modal_show {
  visibility: visible;
  opacity: 1;
}